@font-face {
  font-family: "Bebas";
  font-display: swap;
  src: url("../../fonts/BebasNeue-Regular.woff2") format("woff2"), url("../../fonts/BebasNeue-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "PTSans";
  font-display: swap;
  src: url("../../fonts/PTSans-Regular.woff2") format("woff2"), url("../../fonts/PTSans-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Akziden";
  font-display: swap;
  src: url("../../fonts/AkzidenzGroteskBQCondensed-Bold.woff2") format("woff2"), url("../../fonts/AkzidenzGroteskBQCondensed-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

/*stylelint-disable*/

html {
  -webkit-text-size-adjust: 100%;
}

hr {
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: inherit;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: inherit;
}

small {
  font-size: inherit;
}

sub,
sup {
  font-size: inherit;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

legend {
  color: inherit;
  display: table;
  max-width: 100%;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

main,
nav,
footer,
header,
aside {
  display: block;
}

button {
  cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%);
}

.container {
  max-width: 910px;
  margin: 0 auto;
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
}

html,
body {
  font-family: "Akziden", "Arial", sans-serif;
  font-size: 25px;
  font-weight: 700;
  line-height: 25px;
  color: #000000;
  height: 100%;
  width: 100%;
  min-width: 320px;
  background-color: #ffffff;
}

img {
  vertical-align: top;
  border-style: none;
  max-width: 100%;
  height: auto;
}

.wrapper {
  min-height: 100%;
  overflow: hidden;
}

.content {
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: auto;
}

.section-title {
  color: #ffffff;
  font-size: 50px;
  line-height: 50px;
  letter-spacing: 5px;
  text-transform: uppercase;
}

.section-title--black {
  color: #000000;
}

.item-title {
  text-transform: uppercase;
  letter-spacing: 2.5px;
}

.item-title--white {
  color: #ffffff;
}

.section-text {
  font-family: "PTSans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 25px;
  font-weight: 400;
}

.section-text--white {
  color: #ffffff;
}

.section-link {
  font-family: "Bebas", "Arial", sans-serif;
  font-size: 15px;
  line-height: 25px;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  color: #000000;
  transition: all 0.5s ease 0s;
}

.section-link:hover,
.section-link:focus {
  color: rgba(0, 0, 0, 0.6);
  transition: all 0.5s ease 0s;
  outline: none;
}

.section-link:active {
  color: rgba(0, 0, 0, 0.3);
  transition: all 0.1s ease 0s;
}

.section-link--white {
  color: #ffffff;
}

.section-link--white:hover,
.section-link--white:focus {
  color: rgba(255, 255, 255, 0.6);
  transition: all 0.5s ease 0s;
  outline: none;
}

.section-link--white:active {
  color: rgba(255, 255, 255, 0.3);
  transition: all 0.1s ease 0s;
}

.section-btn {
  width: 155px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Bebas", "Arial", sans-serif;
  font-size: 15px;
  line-height: 25px;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  background-color: #000000;
  color: #ffffff;
  transition: all 0.5s ease 0s;
  text-decoration: none;
}

.section-btn:hover,
.section-btn:focus {
  background-color: rgba(0, 0, 0, 0.8);
  transition: all 0.5s ease 0s;
  outline: none;
}

.section-btn:active {
  background-color: rgba(0, 0, 0, 0.6);
  transition: all 0.1s ease 0s;
}

.section-btn--white {
  background-color: #ffffff;
  color: #000000;
}

.section-btn--white:hover,
.section-btn--white:focus {
  background-color: rgba(255, 255, 255, 0.8);
}

.section-btn--white:active {
  background-color: rgba(255, 255, 255, 0.6);
}

body.lock {
  overflow: hidden;
}

.ibg {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}

.ibg img {
  width: 0;
  height: 0;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
}

.main-header {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 3;
  right: 0;
  min-height: 175px;
  padding-top: 29px;
  padding-bottom: 30px;
}

.main-header::before {
  position: absolute;
  content: "";
  width: 100%;
  min-height: 140px;
  top: 0;
  left: 0;
  background-color: transparent;
  z-index: -1;
}

.main-header .container {
  display: flex;
  flex-direction: column;
}

.main-header__top {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}

.main-header__burger {
  display: none;
}

.main-header__btn {
  width: 20px;
  height: 20px;
  padding: 0;
  font-family: "Bebas", "Arial", sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 10px;
  color: #000000;
  letter-spacing: 1px;
  text-transform: uppercase;
  background-color: transparent;
  border: none;
  text-decoration: none;
}

.main-header__btn.active {
  color: #ffffff;
  background-color: #000000;
}

.main-header__btn:hover,
.main-header__btn:focus {
  outline: none;
  color: #ffffff;
  background-color: #000000;
  transition: all 0.5s ease 0s;
}

.main-header__btn:active {
  color: rgba(255, 255, 255, 0.8);
  background-color: rgba(0, 0, 0, 0.4);
  transition: all 0.1s ease 0s;
}

.main-header__btn span {
  transition: all 0.5s ease 0s;
  position: relative;
}

.main-header__btn span::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 1px;
  background-color: rgba(0, 0, 0, 0.5);
  bottom: 0;
  left: 0;
  transform: translateY(1px) scale(1, 1);
}

.main-header__link {
  font-size: 15px;
  color: #000000;
  text-transform: uppercase;
  transition: all 0.5s ease 0s;
}

.main-header__link:hover,
.main-header__link:focus {
  outline: none;
  color: rgba(0, 0, 0, 0.6);
  transition: all 0.5s ease 0s;
}

.main-header__link:active {
  color: rgba(0, 0, 0, 0.3);
  transition: all 0.1s ease 0s;
  text-decoration: none;
}

.main-header__logo {
  position: absolute;
  left: 50%;
  top: 40px;
  transform: translateX(-50%);
  z-index: 5;
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding-left: 6px;
  padding-right: 6px;
}

.navigation__list li {
  margin-left: 23px;
  margin-right: 23px;
  line-height: 15px;
}

.navigation__list li:first-child {
  margin-left: 0;
}

.navigation__list li:last-child {
  margin-right: 0;
}

.navigation__list li:nth-child(3) {
  margin-right: 170px;
}

.navigation__list li:nth-child(4) {
  margin-left: 170px;
}

.navigation__link {
  color: #000000;
  font-size: 15px;
  line-height: 15px;
  text-decoration: none;
  text-transform: uppercase;
  transition: all 0.5s ease 0s;
}

.navigation__link:hover,
.navigation__link:focus {
  color: rgba(0, 0, 0, 0.6);
  transition: all 0.5s ease 0s;
  outline: none;
}

.navigation__link:active {
  color: rgba(0, 0, 0, 0.3);
  transition: all 0.1s ease 0s;
}

.navigation--da .main-header__link {
  color: #000000;
  font-size: 15px;
  line-height: 15px;
  text-decoration: none;
  text-transform: uppercase;
  transition: all 0.5s ease 0s;
}

.navigation--da .main-header__link:hover,
.navigation--da .main-header__link:focus {
  color: rgba(0, 0, 0, 0.6);
  transition: all 0.5s ease 0s;
  outline: none;
}

.navigation--da .main-header__link:active {
  color: rgba(0, 0, 0, 0.3);
  transition: all 0.5s ease 0s;
}

.logo__link {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.logo span {
  text-transform: uppercase;
  font-family: "Bebas", "Arial", sans-serif;
  font-size: 8px;
  font-weight: 400;
  line-height: 8px;
  color: #000000;
}

.logo span:first-child {
  letter-spacing: 2.72px;
}

.logo span:last-child {
  letter-spacing: 8px;
}

.lead__row {
  display: flex;
  background-color: #000000;
}

.lead__col {
  flex: 0 0 50%;
}

.lead__wrap {
  height: 100%;
}

.lead--services {
  margin-top: 175px;
}

.lead--services .lead__content {
  padding-top: 72px;
  padding-left: 62px;
  padding-right: 10px;
  padding-bottom: 75px;
}

.lead--services .lead__content-title {
  max-width: 200px;
  margin-bottom: 30px;
}

.lead--services .lead__text {
  max-width: 320px;
  margin-bottom: 48px;
}

.lead--services .lead__links-group {
  display: inline-flex;
  align-items: center;
}

.lead--services .lead__link:first-child {
  margin-right: 14px;
}

.lead--about .lead__content {
  padding-top: 57px;
  padding-left: 10px;
  padding-right: 61px;
  padding-bottom: 57px;
}

.lead--about .lead__content-title {
  text-align: right;
}

.menus {
  padding-top: 75px;
  padding-bottom: 45px;
}

.menus__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.menus__list li {
  flex: 0 0 25%;
  display: flex;
  flex-direction: column;
}

.menus__item {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding-bottom: 35px;
}

.menus__link-img {
  display: block;
  padding-bottom: 108.15%;
  margin-bottom: 25px;
}

.menus__item-title {
  max-width: 185px;
  min-height: 50px;
  margin-bottom: 35px;
}

.menus__text {
  flex: 1 1 auto;
  max-width: 185px;
  margin-bottom: 20px;
  min-height: 80px;
}

.menus__btn {
  margin-bottom: 25px;
}

.features {
  padding-top: 50px;
  padding-bottom: 25px;
  background-color: #ebebeb;
}

.features__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
}

.features__list li {
  flex: 1 1 50%;
  margin-bottom: 25px;
  display: flex;
}

.features__icon {
  flex: 0 0 100px;
  height: 100px;
  background-color: #000000;
  position: relative;
}

.features__icon::before {
  position: absolute;
  content: "";
  left: 50%;
  transform: translateX(-50%);
  background-position: 0 0;
  background-repeat: no-repeat;
}

.features__icon--flower::before {
  width: 49px;
  height: 70px;
  top: 12px;
  background-image: url("../../img/flower.png");
}

.features__icon--time::before {
  width: 70px;
  height: 70px;
  top: 15px;
  background-image: url("../../img/time.png");
}

.features__icon--leaf::before {
  width: 59px;
  height: 68px;
  top: 16px;
  background-image: url("../../img/leaf.png");
  background-size: cover;
}

.features__icon--fit::before {
  width: 60px;
  height: 64px;
  top: 17px;
  background-image: url("../../img/fit.png");
  background-size: cover;
}

.features__content {
  padding-left: 10px;
  padding-right: 12px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.contacts {
  padding-top: 80px;
  padding-bottom: 24px;
}

.contacts__title {
  margin-bottom: 30px;
}

.contacts__row {
  display: flex;
}

.contacts__col {
  flex: 0 0 50%;
  font-family: "PTSans", "Arial", sans-serif;
  font-size: 15px;
  font-weight: 400;
}

.contacts__adress {
  margin-bottom: 25px;
  display: flex;
  align-items: center;
}

.contacts__adress span {
  padding-left: 10px;
}

.contacts__phone {
  display: flex;
  align-items: center;
}

.contacts__phone a {
  padding-left: 10px;
  color: #000000;
  text-decoration: none;
}

.contacts__mail {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
}

.contacts__mail a {
  padding-left: 10px;
  color: #000000;
  text-decoration: none;
}

.contacts__social-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.contacts__social-list li {
  margin-left: 15px;
  margin-right: 15px;
}

.contacts__social-list li:first-child {
  margin-left: 0;
}

.contacts__social-list li:last-child {
  margin-right: 0;
}

.contacts__social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background-color: #000000;
  transition: all 0.5s ease 0s;
}

.contacts__social-link:hover,
.contacts__social-link:focus {
  background-color: rgba(0, 0, 0, 0.6);
  transition: all 0.5s ease 0s;
  outline: none;
}

.contacts__social-link:hover svg,
.contacts__social-link:focus svg {
  fill: rgba(255, 255, 255, 0.6);
  transition: all 0.5s ease 0s;
}

.contacts__social-link:active {
  background-color: rgba(0, 0, 0, 0.3);
  transition: all 0.1s ease 0s;
}

.contacts__social-link:active svg {
  fill: rgba(255, 255, 255, 0.6);
  transition: all 0.5s ease 0s;
}

.form {
  background-color: #000000;
}

.form__row {
  display: flex;
}

.form__col {
  flex: 0 0 50%;
}

.form__col:last-child {
  display: flex;
  flex-direction: column;
}

.form__map-img {
  width: 683px;
  height: 475px;
}

.form__map-wrap {
  height: 100%;
}

.form__wrap {
  padding-top: 54px;
  padding-left: 60px;
  padding-bottom: 50px;
  padding-right: 10px;
}

.form__title {
  margin-bottom: 25px;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 2.5px;
}

.form__content {
  display: flex;
  flex-direction: column;
  max-width: 374px;
}

.form__input {
  margin-bottom: 25px;
  padding-top: 11px;
  padding-bottom: 10px;
  padding-left: 20px;
  padding-right: 20px;
  background-color: #252525;
  border: 2px solid #959595;
  font-family: "PTSans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 25px;
  font-weight: 400;
  color: #ffffff;
}

.form__input::placeholder {
  color: #959595;
}

.form__input--area {
  min-height: 100px;
}

.form__btn {
  border: none;
}

.main-footer {
  padding-top: 34px;
  padding-bottom: 27px;
}

.main-footer__row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main-footer__text {
  font-family: "PTSans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 25px;
  font-weight: 400;
  color: #4c4c4c;
}

.main-footer__text a {
  color: #4c4c4c;
}

@media (min-width: 659.98px) {
  .navigation__link {
    position: relative;
  }

  .navigation__link::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 1px;
    background-color: rgba(0, 0, 0, 0.6);
    bottom: 0;
    left: 0;
    transform: translateY(4px) scale(0);
  }

  .navigation__link:hover::after,
  .navigation__link:focus::after {
    transform: translateY(4px) scale(1, 1);
    transition: all 0.5s ease 0s;
  }

  .navigation__link:active::after {
    background-color: rgba(0, 0, 0, 0.3);
    transform: translateY(4px) scale(1, 1);
    transition: all 0.1s ease 0s;
  }

  .navigation--da .main-header__link {
    position: relative;
  }

  .navigation--da .main-header__link::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 1px;
    background-color: rgba(0, 0, 0, 0.6);
    bottom: 0;
    left: 0;
    transform: translateY(4px) scale(0);
  }

  .navigation--da .main-header__link:hover::after,
  .navigation--da .main-header__link:focus::after {
    transform: translateY(4px) scale(1, 1);
    transition: all 0.5s ease 0s;
  }

  .navigation--da .main-header__link:active::after {
    background-color: rgba(0, 0, 0, 0.3);
    transform: translateY(4px) scale(1, 1);
    transition: all 0.1s ease 0s;
  }
}

@media (max-width: 849.98px) {
  .navigation__list li {
    margin-left: 12px;
    margin-right: 12px;
  }

  .navigation__list li:nth-child(3) {
    margin-right: 120px;
  }

  .navigation__list li:nth-child(4) {
    margin-left: 120px;
  }

  .menus {
    padding-top: 45px;
    padding-bottom: 25px;
  }

  .contacts {
    padding-top: 40px;
  }

  .form__row {
    flex-direction: column;
  }
}

@media (max-width: 829.98px) {
  .features__list li {
    flex-wrap: wrap;
    justify-content: center;
  }

  .features__icon {
    margin-bottom: 14px;
  }

  .features__content {
    align-items: center;
  }

  .features__item-title {
    text-align: center;
  }

  .features__descr {
    text-align: center;
  }
}

@media (max-width: 659.98px) {
  .main-header.active::before {
    background-color: #ffffff;
    z-index: 4;
  }

  .main-header__burger {
    display: block;
    width: 30px;
    height: 20px;
    z-index: 4;
    background-color: transparent;
    position: absolute;
    top: 34px;
    left: 24px;
    border: none;
    padding: 0;
    cursor: pointer;
  }

  .main-header__burger span {
    background-color: #000000;
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    top: 9px;
    transition: all 0.3s ease 0s;
  }

  .main-header__burger span:hover,
  .main-header__burger span:focus {
    outline: none;
  }

  .main-header__burger::before,
  .main-header__burger::after {
    content: "";
    background-color: #000000;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    transition: all 0.3s ease 0s;
  }

  .main-header__burger::before {
    top: 0;
  }

  .main-header__burger::after {
    bottom: 0;
  }

  .main-header__burger.active span {
    transform: scale(0);
  }

  .main-header__burger.active::before {
    transform: rotate(45deg);
    top: 9px;
    background-color: #000000;
  }

  .main-header__burger.active::after {
    transform: rotate(-45deg);
    bottom: 9px;
    background-color: #000000;
  }

  .main-header__burger:hover,
  .main-header__burger:focus {
    outline: none;
  }

  .main-header__logo {
    top: 16px;
  }

  .main-header {
    padding-bottom: 0;
    padding-top: 0;
    min-height: 100px;
  }

  .navigation {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 0;
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    transition: all 0.4s ease 0s;
    background-color: rgba(255, 255, 255, 0.8);
    padding: 160px 10px 20px 10px;
    z-index: 2;
  }

  .navigation.active {
    top: 0;
  }

  .navigation__list {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .navigation__list li {
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 20px;
  }

  .navigation__list li:nth-child(3) {
    margin-right: 0;
  }

  .navigation__list li:nth-child(4) {
    margin-left: 0;
  }

  .navigation__link {
    font-size: 25px;
    font-weight: 700;
    line-height: 1.4;
    color: #000000;
  }

  .navigation__link:hover,
  .navigation__link:focus {
    color: #000000;
  }

  .navigation__link:active {
    color: #000000;
    transition: all 0.1s ease 0s;
  }

  .navigation--da .main-header__btn {
    width: 40px;
    height: 40px;
    font-size: 25px;
  }

  .navigation--da .main-header__link {
    margin-bottom: 20px;
    color: #000000;
    font-size: 25px;
    line-height: 25px;
    text-decoration: none;
    transition: all 0.5s ease 0s;
  }

  .navigation--da .main-header__link:hover,
  .navigation--da .main-header__link:focus {
    color: rgba(0, 0, 0, 0.6);
    transition: all 0.5s ease 0s;
    outline: none;
  }

  .navigation--da .main-header__link:active {
    color: rgba(0, 0, 0, 0.3);
    transition: all 0.5s ease 0s;
  }

  .lead__row {
    flex-direction: column;
  }

  .lead--services .lead__col:first-child {
    display: none;
  }

  .lead--services .lead__col:last-child {
    flex: 1 1 100%;
  }

  .lead--services .lead__content {
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 47px;
    padding-bottom: 45px;
  }

  .lead--services .lead__content-title {
    max-width: none;
  }

  .lead--services .lead__text {
    max-width: none;
  }

  .lead--services {
    margin-top: 140px;
  }

  .lead--about .lead__col:first-child {
    flex: 1 1 100%;
  }

  .lead--about .lead__col:last-child {
    display: none;
  }

  .lead--about .lead__content {
    padding-left: 30px;
    padding-right: 31px;
    padding-top: 37px;
    padding-bottom: 37px;
  }

  .lead--about .lead__content-title {
    text-align: left;
  }

  .menus__list {
    flex-wrap: wrap;
  }

  .menus__list li {
    flex: 0 0 50%;
  }

  .contacts {
    padding-top: 20px;
  }

  .contacts__row {
    flex-wrap: wrap;
  }

  .contacts__col:first-child {
    margin-bottom: 30px;
  }

  .contacts__col {
    flex: 0 0 100%;
  }

  .form__map-wrap {
    height: 320px;
  }

  .form__wrap {
    padding-top: 24px;
    padding-left: 40px;
    padding-bottom: 30px;
    padding-right: 15px;
  }

  .main-footer__row {
    flex-direction: column;
  }

  .main-footer__col:first-child {
    order: 2;
  }

  .main-footer__col:last-child {
    order: 1;
    margin-bottom: 10px;
  }

  .main-footer__text {
    text-align: center;
    display: flex;
    flex-direction: column;
  }
}

@media (max-width: 449.98px) {
  .main-header.active::before {
    min-height: 100px;
  }

  .main-header__logo {
    transform: translateX(-40%);
  }

  .logo span {
    display: none;
  }

  .lead--services {
    margin-top: 100px;
  }

  .menus__list li {
    flex: 1 1 100%;
  }

  .menus__item-title {
    margin-bottom: 15px;
  }

  .menus__item-title {
    max-width: none;
    min-height: auto;
  }

  .menus__text {
    max-width: none;
    min-height: auto;
  }

  .features__list li {
    flex: 1 1 100%;
  }

  .form__col:last-child {
    align-items: center;
  }

  .form__wrap {
    padding-left: 15px;
    width: 100%;
  }

  .form__content {
    max-width: none;
    width: 100%;
  }
}

@media (max-width: 659.98px) and (min-width: 659.98px) {
  .navigation--da .main-header__link {
    position: relative;
  }

  .navigation--da .main-header__link::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 1px;
    background-color: rgba(0, 0, 0, 0.6);
    bottom: 0;
    left: 0;
    transform: translateY(4px) scale(0);
  }

  .navigation--da .main-header__link:hover::after,
  .navigation--da .main-header__link:focus::after {
    transform: translateY(4px) scale(1, 1);
    transition: all 0.5s ease 0s;
  }

  .navigation--da .main-header__link:active::after {
    background-color: rgba(0, 0, 0, 0.3);
    transform: translateY(4px) scale(1, 1);
    transition: all 0.1s ease 0s;
  }
}